const eventEmitter = {
  list: {},
  on(key, fn) {
    console.log(key)
    if (!this.list[key]) {
      this.list[key] = [];
    }
    this.list[key].push(fn);
  },
  emit(type:string, ...rests) {
    let key = type,
      fns = this.list[key];

    if (!fns || fns.length === 0) {
      return false;
    }
    fns.forEach(fn => {
      fn.apply(this, ...rests);
    });
  },
  remove(key, fn?) {
    // 
    let fns = this.list[key];
    // false
    if (!fns) return false;
    // 
    // key
    if (!fn) {
      fns && (fns.length = 0);
    } else {
      // fn
      // 
      fns.forEach((cb, i) => {
        if (cb === fn) {
          fns.splice(i, 1);
        }
      });
    }
  },
  has(key) {
    return this.list[key]
  }
}

export default eventEmitter
